"use client"

import React, { useEffect } from "react"
import { useState } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { motion } from "framer-motion"
import { Wallet, ArrowLeft, Check, Loader2, Shield } from "lucide-react"
import { useAuth } from "@/lib/auth-context"
import { GoogleSignIn } from "@/components/google-sign-in"

type Step = "auth" | "2fa" | "register" | "pin"

interface GoogleUserData {
  email: string
  name: string
  picture: string
}

interface Pending2FAUser {
  userId: string
  email: string
  name: string
  picture: string
}

export default function LoginPage() {
  const router = useRouter()
  const { login, isAuthenticated, isLoading: authLoading } = useAuth()
  const [step, setStep] = useState<Step>("auth")
  const [isLoading, setIsLoading] = useState(false)
  const [userData, setUserData] = useState<GoogleUserData | null>(null)
  const [pending2FAUser, setPending2FAUser] = useState<Pending2FAUser | null>(null)
  const [twoFactorCode, setTwoFactorCode] = useState(["", "", "", "", "", ""])
  const [twoFactorError, setTwoFactorError] = useState("")
  
  // PIN state for registration
  const [pin, setPin] = useState(["", "", "", "", "", ""])
  const [confirmPin, setConfirmPin] = useState(["", "", "", "", "", ""])
  const [pinError, setPinError] = useState("")
  
  // Registration form state
  const [formData, setFormData] = useState({
    nomeCompleto: "",
    cpf: "",
    dataNascimento: "",
    telefone: "",
    termos: false,
  })

  // Redirect if already logged in
  useEffect(() => {
    if (!authLoading && isAuthenticated) {
      router.push("/dashboard")
    }
  }, [authLoading, isAuthenticated, router])

  const handleGoogleSuccess = async (googleUser: {
    googleId: string
    email: string
    name: string
    picture: string
  }) => {
    setIsLoading(true)
    
    const googleUserData: GoogleUserData = {
      email: googleUser.email,
      name: googleUser.name,
      picture: googleUser.picture,
    }
    
    setUserData(googleUserData)
    
    try {
      // Call API to check if user exists
      const response = await fetch("/api/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          action: "google-login",
          email: googleUser.email,
          googleId: googleUser.googleId,
        }),
      })
      
      const data = await response.json()
      
      if (data.error) {
        console.error("Login error:", data.error)
        setIsLoading(false)
        return
      }
      
      // User exists and logged in
      if (data.success && !data.isNewUser) {
        // Check if 2FA is required
        if (data.requires2FA) {
          setPending2FAUser({
            userId: data.userId,
            email: data.userEmail,
            name: data.userName,
            picture: data.userPicture,
          })
          setStep("2fa")
          setIsLoading(false)
          return
        }
        
        // Login successful
        login({
          id: data.user.id,
          email: data.user.email,
          name: data.user.nomeCompleto || data.user.name,
          picture: data.user.photoURL || data.user.picture || "",
          saldo: data.user.saldo ?? 0,
          twoFactorEnabled: data.user.twoFactorEnabled || false,
        }, data.accessToken)
        
        // Store refresh token is handled by cookie
        router.push("/dashboard")
        return
      }
      
      // New user - show registration form
      if (data.isNewUser) {
        setStep("register")
        setFormData(prev => ({
          ...prev,
          nomeCompleto: googleUserData.name,
        }))
        setIsLoading(false)
        return
      }
    } catch (error) {
      console.error("Error checking user:", error)
      setIsLoading(false)
    }
  }

  const handle2FASubmit = async () => {
    const code = twoFactorCode.join("")
    
    if (code.length !== 6) {
      setTwoFactorError("Digite o codigo completo de 6 digitos")
      return
    }
    
    setIsLoading(true)
    setTwoFactorError("")
    
    try {
      // Call API to verify 2FA
      const response = await fetch("/api/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          action: "verify-2fa",
          userId: pending2FAUser?.userId,
          twoFactorCode: code,
        }),
      })
      
      const data = await response.json()
      
      if (data.error) {
        setTwoFactorError(data.error || "Código 2FA inválido")
        setTwoFactorCode(["", "", "", "", "", ""])
        setIsLoading(false)
        return
      }
      
      if (!data.success) {
        setTwoFactorError("Erro ao verificar código 2FA")
        setIsLoading(false)
        return
      }
      
      // Success - login with API response
      login({
        id: data.user.id,
        email: data.user.email,
        name: data.user.nomeCompleto || data.user.name,
        picture: data.user.photoURL || data.user.picture || "",
        saldo: data.user.saldo ?? 0,
        twoFactorEnabled: data.user.twoFactorEnabled || false,
      }, data.accessToken)
      
      // Redirect to dashboard
      router.push("/dashboard")
    } catch (error) {
      setTwoFactorError("Erro ao verificar codigo")
      setIsLoading(false)
    }
  }

  const handle2FAInput = (index: number, value: string) => {
    if (!/^\d*$/.test(value)) return
    
    const newCode = [...twoFactorCode]
    newCode[index] = value.slice(-1)
    setTwoFactorCode(newCode)
    setTwoFactorError("")
    
    // Auto-focus next input
    if (value && index < 5) {
      const nextInput = document.getElementById(`2fa-${index + 1}`)
      nextInput?.focus()
    }
    
    // Auto-submit when complete
    if (index === 5 && value) {
      const fullCode = newCode.join("")
      if (fullCode.length === 6) {
        setTimeout(() => handle2FASubmit(), 100)
      }
    }
  }

  const handle2FAKeyDown = (index: number, e: React.KeyboardEvent) => {
    if (e.key === "Backspace" && !twoFactorCode[index] && index > 0) {
      const prevInput = document.getElementById(`2fa-${index - 1}`)
      prevInput?.focus()
    }
  }

  const formatCPF = (value: string) => {
    const numbers = value.replace(/\D/g, "")
    return numbers
      .replace(/(\d{3})(\d)/, "$1.$2")
      .replace(/(\d{3})(\d)/, "$1.$2")
      .replace(/(\d{3})(\d{1,2})/, "$1-$2")
      .replace(/(-\d{2})\d+?$/, "$1")
  }

  const formatPhone = (value: string) => {
    const numbers = value.replace(/\D/g, "")
    return numbers
      .replace(/(\d{2})(\d)/, "($1) $2")
      .replace(/(\d{5})(\d)/, "$1-$2")
      .replace(/(-\d{4})\d+?$/, "$1")
  }

  const formatDate = (value: string) => {
    const numbers = value.replace(/\D/g, "")
    return numbers
      .replace(/(\d{2})(\d)/, "$1/$2")
      .replace(/(\d{2})(\d)/, "$1/$2")
      .replace(/(\/\d{4})\d+?$/, "$1")
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, type } = e.target
    let formattedValue = value

    if (name === "cpf") formattedValue = formatCPF(value)
    if (name === "telefone") formattedValue = formatPhone(value)
    if (name === "dataNascimento") formattedValue = formatDate(value)

    if (type === "checkbox") {
      setFormData(prev => ({
        ...prev,
        [name]: (e.target as HTMLInputElement).checked,
      }))
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: formattedValue,
      }))
    }
  }

  const handlePinInput = (index: number, value: string, isConfirm: boolean = false) => {
    if (!/^\d*$/.test(value)) return
    
    const setter = isConfirm ? setConfirmPin : setPin
    const currentPin = isConfirm ? confirmPin : pin
    
    const newPin = [...currentPin]
    newPin[index] = value.slice(-1)
    setter(newPin)
    setPinError("")
    
    // Auto-focus next input
    if (value && index < 5) {
      const nextInput = document.getElementById(`${isConfirm ? "confirm-pin" : "pin"}-${index + 1}`)
      nextInput?.focus()
    }
  }

  const handlePinKeyDown = (index: number, e: React.KeyboardEvent, isConfirm: boolean = false) => {
    const currentPin = isConfirm ? confirmPin : pin
    if (e.key === "Backspace" && !currentPin[index] && index > 0) {
      const prevInput = document.getElementById(`${isConfirm ? "confirm-pin" : "pin"}-${index - 1}`)
      prevInput?.focus()
    }
  }

  const handleRegisterSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!formData.termos) {
      return
    }
    
    // Move to PIN creation step
    setStep("pin")
  }

  const handlePinSubmit = async () => {
    const pinValue = pin.join("")
    const confirmPinValue = confirmPin.join("")
    
    if (pinValue.length !== 6) {
      setPinError("Digite um PIN de 6 digitos")
      return
    }
    
    if (pinValue !== confirmPinValue) {
      setPinError("Os PINs nao coincidem")
      return
    }
    
    setIsLoading(true)
    
    try {
      // Call register API
      const response = await fetch("/api/auth/register", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          googleId: userData?.email, // Using email as googleId for now
          email: userData?.email || "",
          nomeCompleto: formData.nomeCompleto,
          cpf: formData.cpf,
          dataNascimento: formData.dataNascimento,
          telefone: formData.telefone,
          pin: pinValue,
          photoURL: userData?.picture || "",
        }),
      })
      
      const data = await response.json()
      
      if (!response.ok || data.error) {
        setPinError(data.error || "Erro ao criar conta")
        setIsLoading(false)
        return
      }
      
      // User created successfully - login with API response
      login({
        id: data.user.id,
        email: data.user.email,
        name: data.user.nomeCompleto,
        picture: data.user.photoURL || "",
        cpf: formData.cpf,
        telefone: formData.telefone,
        saldo: data.user.saldo ?? 0, // Always 0 for new users
        twoFactorEnabled: data.user.twoFactorEnabled || false,
      }, data.accessToken)
      
      // Refresh token is stored in httpOnly cookie by server
      
      // Redirect to dashboard
      router.push("/dashboard")
    } catch (error) {
      setPinError("Erro ao criar conta. Tente novamente.")
      setIsLoading(false)
    }
  }

  // Show loading while checking auth
  if (authLoading) {
    return (
      <main className="min-h-screen bg-[#09090B] flex items-center justify-center">
        <div className="w-8 h-8 border-2 border-emerald-500 border-t-transparent rounded-full animate-spin" />
      </main>
    )
  }

  return (
    <main className="min-h-screen bg-[#09090B] flex">
      {/* Left Side - Branding */}
      <div className="hidden lg:flex lg:w-1/2 bg-gradient-to-br from-emerald-500/10 via-transparent to-transparent p-12 flex-col justify-between relative overflow-hidden">
        <div className="absolute inset-0 bg-[url('/images/precision-grid.png')] opacity-5" />
        
        <Link href="/" className="flex items-center gap-2 relative z-10">
          <div className="w-10 h-10 rounded-xl bg-emerald-500 flex items-center justify-center">
            <Wallet className="w-5 h-5 text-white" />
          </div>
          <span className="text-white font-semibold text-xl">fluxpay</span>
        </Link>

        <div className="relative z-10">
          <h1 
            className="text-4xl xl:text-5xl text-white mb-6"
            style={{
              letterSpacing: "-0.0325em",
              fontVariationSettings: '"opsz" 28',
              fontWeight: 538,
              lineHeight: 1.2,
            }}
          >
            Sua carteira digital<br />
            <span className="text-emerald-400">simples e segura</span>
          </h1>
          <p className="text-zinc-400 text-lg max-w-md leading-relaxed">
            Deposite, transfira e gerencie seu dinheiro com total transparencia e taxas justas.
          </p>
        </div>

        <div className="relative z-10 flex items-center gap-8">
          <div>
            <p className="text-3xl font-bold text-white">50K+</p>
            <p className="text-zinc-500 text-sm">Usuarios ativos</p>
          </div>
          <div className="w-px h-12 bg-zinc-800" />
          <div>
            <p className="text-3xl font-bold text-white">R$ 2M+</p>
            <p className="text-zinc-500 text-sm">Transacionados</p>
          </div>
          <div className="w-px h-12 bg-zinc-800" />
          <div>
            <p className="text-3xl font-bold text-white">99.9%</p>
            <p className="text-zinc-500 text-sm">Uptime</p>
          </div>
        </div>
      </div>

      {/* Right Side - Auth Form */}
      <div className="w-full lg:w-1/2 flex items-center justify-center p-6 lg:p-12">
        <div className="w-full max-w-md">
          {step === "auth" && (
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.4 }}
            >
              {/* Mobile Logo */}
              <Link href="/" className="flex items-center gap-2 mb-8 lg:hidden">
                <div className="w-8 h-8 rounded-lg bg-emerald-500 flex items-center justify-center">
                  <Wallet className="w-4 h-4 text-white" />
                </div>
                <span className="text-white font-semibold text-lg">fluxpay</span>
              </Link>

              <h2 
                className="text-2xl sm:text-3xl text-white mb-2"
                style={{
                  letterSpacing: "-0.0325em",
                  fontVariationSettings: '"opsz" 28',
                  fontWeight: 538,
                }}
              >
                Entrar ou criar conta
              </h2>
              <p className="text-zinc-400 mb-8">
                Use sua conta Google para continuar
              </p>

              {/* Google Sign-In - Real OAuth only */}
              {process.env.NEXT_PUBLIC_GOOGLE_CLIENT_ID ? (
                <div className="w-full">
                  <GoogleSignIn 
                    onSuccess={handleGoogleSuccess}
                    onError={(error) => {
                      console.error("Google login error:", error)
                      alert("Erro ao fazer login com Google. Por favor, tente novamente.")
                    }}
                  />
                </div>
              ) : (
                <div className="w-full p-4 bg-amber-500/10 border border-amber-500/20 rounded-xl">
                  <p className="text-amber-400 text-sm text-center">
                    Google OAuth não configurado. Configure NEXT_PUBLIC_GOOGLE_CLIENT_ID para habilitar login.
                  </p>
                </div>
              )}

              <div className="mt-8 text-center">
                <p className="text-zinc-500 text-sm">
                  Ao continuar, voce concorda com nossos{" "}
                  <Link href="/termos" className="text-emerald-400 hover:underline">
                    Termos de Uso
                  </Link>{" "}
                  e{" "}
                  <Link href="/termos" className="text-emerald-400 hover:underline">
                    Politica de Privacidade
                  </Link>
                </p>
              </div>
            </motion.div>
          )}

          {step === "2fa" && (
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.4 }}
            >
              <button
                type="button"
                onClick={() => {
                  setStep("auth")
                  setTwoFactorCode(["", "", "", "", "", ""])
                  setTwoFactorError("")
                }}
                className="flex items-center gap-2 text-zinc-400 hover:text-white mb-6 transition-colors"
              >
                <ArrowLeft className="w-4 h-4" />
                Voltar
              </button>

              <div className="w-16 h-16 rounded-2xl bg-emerald-500/10 border border-emerald-500/20 flex items-center justify-center mb-6">
                <Shield className="w-8 h-8 text-emerald-400" />
              </div>

              <h2 
                className="text-2xl sm:text-3xl text-white mb-2"
                style={{
                  letterSpacing: "-0.0325em",
                  fontVariationSettings: '"opsz" 28',
                  fontWeight: 538,
                }}
              >
                Verificacao em duas etapas
              </h2>
              <p className="text-zinc-400 mb-2">
                Digite o codigo de 6 digitos do seu aplicativo autenticador
              </p>
              
              {pending2FAUser && (
                <p className="text-zinc-500 text-sm mb-8">
                  Entrando como <span className="text-white">{pending2FAUser.email}</span>
                </p>
              )}

              <div className="flex gap-2 mb-6">
                {twoFactorCode.map((digit, index) => (
                  <input
                    key={index}
                    id={`2fa-${index}`}
                    type="text"
                    inputMode="numeric"
                    maxLength={1}
                    value={digit}
                    onChange={(e) => handle2FAInput(index, e.target.value)}
                    onKeyDown={(e) => handle2FAKeyDown(index, e)}
                    className={`w-12 h-14 text-center text-xl font-semibold bg-zinc-900/50 border rounded-xl text-white focus:outline-none transition-colors ${
                      twoFactorError 
                        ? "border-red-500/50 focus:border-red-500" 
                        : "border-zinc-800 focus:border-emerald-500/50"
                    }`}
                    autoFocus={index === 0}
                  />
                ))}
              </div>

              {twoFactorError && (
                <p className="text-red-400 text-sm mb-4">{twoFactorError}</p>
              )}

              <button
                type="button"
                onClick={handle2FASubmit}
                disabled={isLoading || twoFactorCode.join("").length !== 6}
                className="w-full flex items-center justify-center gap-2 px-6 py-4 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 font-medium rounded-xl transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {isLoading ? (
                  <Loader2 className="w-5 h-5 animate-spin" />
                ) : (
                  "Verificar"
                )}
              </button>

              <p className="text-zinc-500 text-sm mt-6 text-center">
                Nao consegue acessar seu autenticador?{" "}
                <button type="button" className="text-emerald-400 hover:underline">
                  Usar codigo de recuperacao
                </button>
              </p>
            </motion.div>
          )}

          {step === "register" && (
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.4 }}
            >
              <button
                type="button"
                onClick={() => setStep("auth")}
                className="flex items-center gap-2 text-zinc-400 hover:text-white mb-6 transition-colors"
              >
                <ArrowLeft className="w-4 h-4" />
                Voltar
              </button>

              {userData && (
                <div className="flex items-center gap-4 mb-6 p-4 bg-zinc-900/50 border border-zinc-800 rounded-xl">
                  <img 
                    src={userData.picture || "/placeholder.svg"} 
                    alt={userData.name}
                    className="w-12 h-12 rounded-full"
                  />
                  <div>
                    <p className="text-white font-medium">{userData.name}</p>
                    <p className="text-zinc-500 text-sm">{userData.email}</p>
                  </div>
                  <div className="ml-auto w-6 h-6 rounded-full bg-emerald-500/20 flex items-center justify-center">
                    <Check className="w-4 h-4 text-emerald-400" />
                  </div>
                </div>
              )}

              <h2 
                className="text-2xl sm:text-3xl text-white mb-2"
                style={{
                  letterSpacing: "-0.0325em",
                  fontVariationSettings: '"opsz" 28',
                  fontWeight: 538,
                }}
              >
                Complete seu cadastro
              </h2>
              <p className="text-zinc-400 mb-8">
                Precisamos de mais algumas informacoes para criar sua conta
              </p>

              <form onSubmit={handleRegisterSubmit} className="space-y-4">
                <div>
                  <label htmlFor="nomeCompleto" className="block text-zinc-400 text-sm mb-2">Nome completo</label>
                  <input
                    type="text"
                    id="nomeCompleto"
                    name="nomeCompleto"
                    value={formData.nomeCompleto}
                    onChange={handleInputChange}
                    required
                    className="w-full px-4 py-3 bg-zinc-900/50 border border-zinc-800 rounded-xl text-white placeholder:text-zinc-600 focus:outline-none focus:border-emerald-500/50 transition-colors"
                    placeholder="Seu nome completo"
                  />
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label htmlFor="cpf" className="block text-zinc-400 text-sm mb-2">CPF</label>
                    <input
                      type="text"
                      id="cpf"
                      name="cpf"
                      value={formData.cpf}
                      onChange={handleInputChange}
                      required
                      maxLength={14}
                      className="w-full px-4 py-3 bg-zinc-900/50 border border-zinc-800 rounded-xl text-white placeholder:text-zinc-600 focus:outline-none focus:border-emerald-500/50 transition-colors"
                      placeholder="000.000.000-00"
                    />
                  </div>
                  <div>
                    <label htmlFor="dataNascimento" className="block text-zinc-400 text-sm mb-2">Data de nascimento</label>
                    <input
                      type="text"
                      id="dataNascimento"
                      name="dataNascimento"
                      value={formData.dataNascimento}
                      onChange={handleInputChange}
                      required
                      maxLength={10}
                      className="w-full px-4 py-3 bg-zinc-900/50 border border-zinc-800 rounded-xl text-white placeholder:text-zinc-600 focus:outline-none focus:border-emerald-500/50 transition-colors"
                      placeholder="DD/MM/AAAA"
                    />
                  </div>
                </div>

                <div>
                  <label htmlFor="telefone" className="block text-zinc-400 text-sm mb-2">Telefone</label>
                  <input
                    type="text"
                    id="telefone"
                    name="telefone"
                    value={formData.telefone}
                    onChange={handleInputChange}
                    required
                    maxLength={15}
                    className="w-full px-4 py-3 bg-zinc-900/50 border border-zinc-800 rounded-xl text-white placeholder:text-zinc-600 focus:outline-none focus:border-emerald-500/50 transition-colors"
                    placeholder="(00) 00000-0000"
                  />
                </div>

                <div className="pt-4">
                  <label className="flex items-start gap-3 cursor-pointer">
                    <input
                      type="checkbox"
                      name="termos"
                      checked={formData.termos}
                      onChange={handleInputChange}
                      required
                      className="mt-1 w-4 h-4 rounded border-zinc-700 bg-zinc-900 text-emerald-500 focus:ring-emerald-500/20"
                    />
                    <span className="text-zinc-400 text-sm">
                      Li e concordo com os{" "}
                      <Link href="/termos" className="text-emerald-400 hover:underline">
                        Termos de Uso
                      </Link>{" "}
                      e{" "}
                      <Link href="/termos" className="text-emerald-400 hover:underline">
                        Politica de Privacidade
                      </Link>
                    </span>
                  </label>
                </div>

                <button
                  type="submit"
                  disabled={!formData.termos}
                  className="w-full flex items-center justify-center gap-2 px-6 py-4 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 font-medium rounded-xl transition-colors disabled:opacity-50 disabled:cursor-not-allowed mt-6"
                >
                  Continuar
                </button>
              </form>
            </motion.div>
          )}

          {step === "pin" && (
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.4 }}
            >
              <button
                type="button"
                onClick={() => setStep("register")}
                className="flex items-center gap-2 text-zinc-400 hover:text-white mb-6 transition-colors"
              >
                <ArrowLeft className="w-4 h-4" />
                Voltar
              </button>

              <div className="w-16 h-16 rounded-2xl bg-emerald-500/10 border border-emerald-500/20 flex items-center justify-center mb-6">
                <Shield className="w-8 h-8 text-emerald-400" />
              </div>

              <h2 
                className="text-2xl sm:text-3xl text-white mb-2"
                style={{
                  letterSpacing: "-0.0325em",
                  fontVariationSettings: '"opsz" 28',
                  fontWeight: 538,
                }}
              >
                Crie seu PIN de seguranca
              </h2>
              <p className="text-zinc-400 mb-8">
                Este PIN sera usado para confirmar transferencias e operacoes sensiveis
              </p>

              <div className="space-y-6">
                <div>
                  <label className="block text-zinc-400 text-sm mb-3">Digite seu PIN de 6 digitos</label>
                  <div className="flex gap-2">
                    {pin.map((digit, index) => (
                      <input
                        key={index}
                        id={`pin-${index}`}
                        type="password"
                        inputMode="numeric"
                        maxLength={1}
                        value={digit}
                        onChange={(e) => handlePinInput(index, e.target.value, false)}
                        onKeyDown={(e) => handlePinKeyDown(index, e, false)}
                        className="w-12 h-14 text-center text-xl font-semibold bg-zinc-900/50 border border-zinc-800 rounded-xl text-white focus:outline-none focus:border-emerald-500/50 transition-colors"
                        autoFocus={index === 0}
                      />
                    ))}
                  </div>
                </div>

                <div>
                  <label className="block text-zinc-400 text-sm mb-3">Confirme seu PIN</label>
                  <div className="flex gap-2">
                    {confirmPin.map((digit, index) => (
                      <input
                        key={index}
                        id={`confirm-pin-${index}`}
                        type="password"
                        inputMode="numeric"
                        maxLength={1}
                        value={digit}
                        onChange={(e) => handlePinInput(index, e.target.value, true)}
                        onKeyDown={(e) => handlePinKeyDown(index, e, true)}
                        className={`w-12 h-14 text-center text-xl font-semibold bg-zinc-900/50 border rounded-xl text-white focus:outline-none transition-colors ${
                          pinError 
                            ? "border-red-500/50 focus:border-red-500" 
                            : "border-zinc-800 focus:border-emerald-500/50"
                        }`}
                      />
                    ))}
                  </div>
                </div>

                {pinError && (
                  <p className="text-red-400 text-sm">{pinError}</p>
                )}

                <button
                  type="button"
                  onClick={handlePinSubmit}
                  disabled={isLoading || pin.join("").length !== 6 || confirmPin.join("").length !== 6}
                  className="w-full flex items-center justify-center gap-2 px-6 py-4 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 font-medium rounded-xl transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  {isLoading ? (
                    <Loader2 className="w-5 h-5 animate-spin" />
                  ) : (
                    "Criar conta"
                  )}
                </button>
              </div>
            </motion.div>
          )}
        </div>
      </div>
    </main>
  )
}
