"use client"

import { useEffect, useCallback } from "react"

declare global {
  interface Window {
    google?: {
      accounts: {
        id: {
          initialize: (config: GoogleConfig) => void
          renderButton: (element: HTMLElement, options: ButtonOptions) => void
          prompt: () => void
        }
      }
    }
  }
}

interface GoogleConfig {
  client_id: string
  callback: (response: GoogleCredentialResponse) => void
  auto_select?: boolean
  cancel_on_tap_outside?: boolean
}

interface ButtonOptions {
  theme?: "outline" | "filled_blue" | "filled_black"
  size?: "large" | "medium" | "small"
  type?: "standard" | "icon"
  shape?: "rectangular" | "pill" | "circle" | "square"
  text?: "signin_with" | "signup_with" | "continue_with" | "signin"
  width?: number
  logo_alignment?: "left" | "center"
}

interface GoogleCredentialResponse {
  credential: string
  select_by: string
}

interface GoogleSignInProps {
  onSuccess: (userData: {
    googleId: string
    email: string
    name: string
    picture: string
  }) => void
  onError?: (error: string) => void
}

export function GoogleSignIn({ onSuccess, onError }: GoogleSignInProps) {
  const handleCredentialResponse = useCallback(async (response: GoogleCredentialResponse) => {
    try {
      // Decode JWT to get user info
      const parts = response.credential.split(".")
      if (parts.length !== 3) {
        onError?.("Invalid token format")
        return
      }

      const payload = JSON.parse(atob(parts[1]))
      
      onSuccess({
        googleId: payload.sub,
        email: payload.email,
        name: payload.name,
        picture: payload.picture,
      })
    } catch (error) {
      console.error("Error processing Google credential:", error)
      onError?.("Failed to process Google login")
    }
  }, [onSuccess, onError])

  useEffect(() => {
    // Load Google Identity Services script
    const script = document.createElement("script")
    script.src = "https://accounts.google.com/gsi/client"
    script.async = true
    script.defer = true
    document.body.appendChild(script)

    script.onload = () => {
      const clientId = process.env.NEXT_PUBLIC_GOOGLE_CLIENT_ID
      
      if (!clientId || !window.google) {
        console.log("[Google] Client ID not configured, using demo mode")
        return
      }

      window.google.accounts.id.initialize({
        client_id: clientId,
        callback: handleCredentialResponse,
        auto_select: false,
        cancel_on_tap_outside: true,
      })

      const buttonDiv = document.getElementById("google-signin-button")
      if (buttonDiv) {
        window.google.accounts.id.renderButton(buttonDiv, {
          theme: "filled_black",
          size: "large",
          type: "standard",
          shape: "rectangular",
          text: "continue_with",
          width: 400,
          logo_alignment: "left",
        })
      }
    }

    return () => {
      document.body.removeChild(script)
    }
  }, [handleCredentialResponse])

  return <div id="google-signin-button" className="w-full" />
}
